<template>
  <div class="order">
    <van-tabs
      :sticky="true"
      v-model="activeName"
      @change="selectOrderStatus"
      color="#e4393c"
      line-height="2"
      title-active-color="#e4393c"
    >
      <van-tab
        :title="item.title"
        :name="item.name"
        v-for="(item, index) in tabContent"
        :key="index"
      >
        <div class="order-content">
          <div class="order-items">
            <!-- 每一订单 start-->
            <div class="orders-item" v-for="(item,index) in orderNos" :key="index">
              <div class="order-item-title">
                <!-- 编号 -->
                <div class="fl order-no">{{item}}</div>
                <!-- 时间 -->
                <div
                  class="order-date fr"
                >{{orderData[item].date | formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
              </div>

              <div class="items" v-for="(v,i) in orderData[item].data" :key="i">
                <div class="clearfix">
                  <div class="fl pro-img">
                    <!-- 图片 -->
                    <img class="auto-img" :src="v.smallImg" alt />
                  </div>
                  <div class="fl pro-info">
                    <div class="names">
                      <!-- 名字 -->
                      <div class="zh-name fl">{{v.name}}</div>
                      <!-- 价格 -->
                      <div class="prices fr">￥{{v.price}}</div>
                    </div>
                    <div class="rules">
                      <!-- 英文名字 -->
                      <div class="en-name fl">{{v.enname}}</div>
                      <!-- 数量 -->
                      <div class="count fr">x{{v.count}}</div>
                    </div>
                    <!-- 商品规格 -->
                    <div class="rule fl">商品规格：{{v.rule}}</div>
                  </div>
                </div>
              </div>

              <div class="orders-total clearfix">
                <div class="fr clearfix">
                  <span class="fl total-count">共计 {{orderData[item].count}} 件商品 合计：</span>
                  <span class="fl total-price">￥{{orderData[item].total}}</span>
                </div>
              </div>

              <div class="fr" v-if="orderData[item].status == 2">
                <span class="confirm">已收货</span>
                <span class="delete">
                  <van-icon
                    class="delete-icon"
                    @click="removeOrder(item)"
                    color="#999"
                    name="delete"
                  />
                </span>
              </div>
              <div class="fr" v-else>
                <span class="confirm" @click="recieve(item)">确认收货</span>
              </div>
            </div>
            <!-- 每一订单 end-->
          </div>
        </div>
        <van-empty v-if="orderNos.length == 0" :image="require('../../assets/images/order.png')" description="没有订单数据" />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../../assets/css/order.less";
export default {
  data() {
    return {
      tabContent: [
        {
          title: "全部",
          name: "0"
        },
        {
          title: "待收货",
          name: "1"
        },
        {
          title: "已收货",
          name: "2"
        }
      ],
      activeName: "0",
      isRecieve: false,
      //订单编号
      orderNos: [],

      // 订单数据
      orderData: {}
    };
  },
  created() {
    //获取订单商品
    this.getOrderProduct();
  },
  methods: {
    //切换不同状态的商品
    selectOrderStatus(a) {
      
      this.getOrderProduct();
    },

    //获取订单商品
    getOrderProduct() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.activeName
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 70000) {
            //获取所有订单编号
            let oids = [];
            result.data.result.forEach(v => {
              if (oids.indexOf(v.oid) == -1) {
                oids.push(v.oid);
              }
            });
            this.orderNos = oids;
            // 按照订单编号筛选商品
            let orderData = {};
            result.data.result.forEach(v => {
              if (!orderData[v.oid]) {
                //如果当前订单数据不存在,则添加一个新订单
                orderData[v.oid] = {
                  data: [v],
                  status: v.status,
                  date: v.createdAt,
                  count: v.count,
                  total: v.count * v.price
                };
              } else {
                orderData[v.oid].count += v.count;
                orderData[v.oid].total += v.count * v.price;
                orderData[v.oid].data.push(v);
              }
            });
            
            

            this.orderData = orderData;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //确认收货
    recieve(oid) {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 80000) {
            this.orderData[oid].status = 2;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //删除订单
    removeOrder(oid) {
      //订单编号
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          

          if (result.data.code == 90000) {
            //删除页面的订单
            let index = this.orderNos.indexOf(oid);
            this.orderNos.splice(index, 1);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>